@use '../../../variables' as var;
.LatestParticipantList {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.list {
  width: 100%;
  max-width: 700px;
  margin-top: 30px;
  background: #1c152e;
  border: 1px solid #2d2d2d;
  border-radius: 24px;
  padding: 25px;
  text-align: left;
  font-weight: 700;
  font-size: 16px;
  line-height: 200%;
  color: white;
}
.time {
  text-align: right;
}
.listTitle {
  opacity: 0.5;
}
.loadMore {
  font-weight: 700;
  font-size: 16px;
  line-height: 200%;
  text-align: center;
  color: #3e44cf;
  cursor: pointer;
}

@media (max-width: var.$mobile-break-point) {
  .list {
    font-size: 14px;
  }
}
