@use '../../variables' as var;

.Rewards {
  position: relative;
  width: 100%;
  padding: 75px 0px 1px;
  background: #1c152e;
  min-height: 100vh;
}
.banner {
  background-position: center bottom;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
}
.bannerContainer {
  position: relative;
  padding: 50px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  img {
    width: 30%;
  }
}
.textWrap {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  gap: 20px;
  text-align: left;
  flex: 1;
  h2 {
    font-weight: 900;
    font-size: 32px;
    line-height: 120%;
    text-transform: uppercase;
    color: #492756;
  }
  h1 {
    font-weight: 900;
    font-size: 40px;
    line-height: 100%;
    /* identical to box height, or 72px */
    letter-spacing: -0.02em;
    color: #fffef9;
  }
}
.mainContent {
  margin-top: -30px;
  padding-top: 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 30px;
}
.statsWrap {
  width: 100%;
}
.statsItem {
  background: #100a1e;
  img {
    width: 40px;
  }
}
.descWrap {
  width: 100%;
}
.descItem {
  width: 100%;
  background: #100a1e;
  border-radius: 10px;
  padding: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 5px;
  .percentage {
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    background: linear-gradient(277.23deg, #05c8c8 0%, #3e44cf 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
  }
  .descCountWrap {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 5px;

    h2 {
      font-size: 16px;
      line-height: 18px;
    }
    .count {
      background: linear-gradient(277.23deg, #05c8c8 0%, #3e44cf 100%);
      border-radius: 10px;
      width: 50px;
      height: 50px;
      line-height: 50px;
      font-weight: 700;
      font-size: 24px;
      color: #ffffff;
    }
  }
}

@media (max-width: var.$mobile-break-point) {
  .Rewards {
    padding-top: 60px;
  }
  .bannerContainer {
    flex-direction: column;
    img {
      position: absolute;
      width: 80%;
      max-width: 300px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      opacity: 0.5;
    }
  }
  .textWrap {
    z-index: 1;
    h2 {
      font-size: 24px;
    }
    h1 {
      font-size: 32px;
    }
  }
}
