.RewardsPoolAction {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 25px;
  > img {
    width: 35px;
  }
}
.title {
  font-weight: 700;
  font-size: 24px;
  /* identical to box height, or 67% */
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
}
.actionWrap {
  max-width: 350px;
  width: 100%;
  border: 2px solid #27224f;
  border-radius: 10px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  h2,
  p {
    font-weight: 700;
    font-size: 16px;
    line-height: 16px;
    /* identical to box height, or 100% */

    text-align: center;
    text-transform: uppercase;

    color: #ffffff;
  }
  p {
    color: #ffcd1d;
  }
}
.claimButton {
  width: 150px;
}
