@use '../../variables' as var;

.Airdrop {
  position: relative;
  width: 100%;
  background-position: center top;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #1c152e;
  background-blend-mode: screen;
}
.mainContent {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.logo {
  display: flex;
  align-items: center;
  img {
    height: 25px;
    margin-right: 15px;
  }
  h2 {
    color: white;
    font-size: 18px;
    line-height: 24px;
    font-weight: 500;
  }
}
.airdropItem {
  width: 100%;
  display: flex;
  gap: 15px;
  justify-content: space-between;
  overflow: hidden;
}
.bannerWrap {
  width: 45%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 20px;
  padding: 20px;
  background-color: #0e0a1a;
  background-blend-mode: screen;
  gap: 20px;
}
.bannerSubtitle {
  font-size: 18px;
  line-height: 22px;
}
.bannerTitle {
  font-size: 24px;
}
.textWrap {
  text-align: left;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
}
.contentWrap {
  width: 45%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  text-align: left;
  gap: 10px;
  .title {
    font-size: 20px;
    line-height: 24px;
    font-weight: 700;
  }
  .desc {
    font-size: 16px;
    line-height: 20px;
    color: #ffffff;
    opacity: 0.5;
  }
}
.actionWrap {
  width: 100%;
  border: 2px solid #27224f;
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
}
.infoWrap {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.infoItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  h2 {
    font-size: 16px;
    font-weight: 700;
  }
  p {
    color: #ffcd1d;
    font-weight: 700;
    font-size: 20px;
  }
}
.button {
  width: 150px;
}
.mobile {
  flex-direction: column;
  border: 2px solid #27224f;
  border-radius: 20px;
  gap: 0;
  .contentWrap,
  .bannerWrap {
    width: 100%;
    border-radius: unset;
  }
  .contentWrap {
    padding: 20px;
  }
  .actionWrap {
    border: none;
    padding: 0;
  }
}
@media (max-width: var.$mobile-break-point) {
  .mainContent {
    padding-top: 80px;
    padding-bottom: 80px;
  }
}
