@use '../../../variables' as var;

@mixin flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Treasury {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 100%;
  background: #27224f;
  backdrop-filter: blur(12px);
  border-radius: 12px;
  // max-width: 1000px;
  width: 100%;
  // margin: 20px auto;
  // padding-bottom: 30px;
  // margin: 30px auto;
  padding: 0 20px;
}

.TreasuryHead {
  position: relative;
  width: 80%;
  max-width: 350px;
  min-width: 250px;
  img {
    width: 100%;
  }

  span {
    position: absolute;
    width: 100%;
    top: 35%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    font-weight: 700;
    font-size: 16px;
    text-transform: uppercase;
  }
}

.TreasuryTitle {
  font-weight: 700;
  font-size: 24px;
  color: #ffffff;
}

.TreasuryValue {
  @include flex;
  width: 100%;
  justify-content: center;
  gap: 10px;
}

.TreasuryValueItem {
  @include flex;
  width: 60px;
  height: 60px;
  background: #1c152e;
  border: 2px solid #38326a;
  border-radius: 4px;
  justify-content: center;
  font-size: 36px;
  font-weight: 700;
  span {
    background: var.$color-title-gradient;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

.TreasuryDesc {
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  color: #bb6ce6;
  max-width: 765px;
}

.LastSold {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 30px;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #ffffff;
  img {
    width: 20px;
  }
  span {
    color: #ffcd1d;
  }
}

@media (max-width: var.$mobile-break-point) {
  .TreasuryTitle {
    font-size: 20px;
  }
  .TreasuryValue {
    gap: 5px;
  }
  .TreasuryValueItem {
    @include flex;
    justify-content: center;
    width: 50px;
    height: 50px;
    font-size: 24px;
  }
  .TreasuryDesc {
    font-size: 14px;
  }
  .LastSold {
    gap: 5px;
    margin-bottom: 20px;

    font-size: 14px;
    img {
      width: 15px;
    }
  }
}
