.Fireworks {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background-color: rgba(0, 0, 0, 0.1);
}
.bottom {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  div {
    width: 500px;
  }
}
.top {
  position: absolute;
  width: 100%;
  min-width: 1500px;
  // height: 100%;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
