.DrawResults {
  width: 100%;
  position: relative;
  user-select: none;
}
.resultTitle {
  margin-bottom: 30px;
}
.slide {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 12px;
  background: #27224f;
  backdrop-filter: blur(12px);
  // padding: 0 20px 20px 20px;
  overflow: hidden;
  gap: 15px;
  padding-bottom: 25px;
}
.slideHeader {
  align-self: flex-start;
  background: linear-gradient(102.16deg, #231f4f 0%, #332d4f 100%);
  // padding: 20px;
  border-bottom-right-radius: 100px;
  min-width: 35%;
  font-weight: 700;
  font-size: 24px;
  line-height: 150%;
  /* identical to box height, or 32px */
  color: #ffcd1d;
  padding: 0 35px;
}

.result {
  width: 100%;
  padding: 0 25px 0 25px;
  display: flex;
  flex-direction: column;
  max-height: 350px;
  overflow-y: scroll;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */

  &::-webkit-scrollbar {
    display: none;
  }
}
.resultItem {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;

  h2,
  p {
    font-weight: 700;
    font-size: 14px;
    margin: 3px 0;
  }
  h2 {
    color: #05c8c8;
  }
}
.title {
  text-align: left;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  /* identical to box height, or 16px */
  color: #ffffff;
  align-self: flex-start;
  margin-left: 25px;
}

:global(.swiper-pagination-bullet) {
  background-color: #05c8c8 !important;
  opacity: 0.6;
}
:global(.swiper-pagination-bullet-active) {
  background-color: #ffcd1d !important;
  opacity: 1;
}
