.Swap {
  position: relative;
  width: 100%;
  background-position: center top;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
}
.mainContent {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.swapWrap {
  background: linear-gradient(102.16deg, #332d4f 0%, #231f4f 100%);
  border-radius: 24px;
  padding: 15px;
  width: 100%;
  max-width: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.swapTitle {
  font-size: 24px;
}
.actionWrap {
  margin-top: 15px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  > img {
    width: 35px;
    margin: 15px 0;
  }
}
.balance {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 5px;
  font-size: 14px;
  margin-bottom: 5px;
  :first-child {
    font-weight: 700;
  }
}
.button {
  margin-top: 15px;
  width: 100%;
}
.input {
  position: relative;
  width: 100%;
  .suffix {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    background-color: #27224f;
    color: white;
    font-weight: 700;
    font-size: 14px;
    line-height: 14px;
    padding: 5px 10px;
    border-radius: 50px;
    margin-right: 5px;
  }
}
