@use '../../../variables' as var;

.RewardsPoolBanner {
  position: relative;
  width: 100%;
  justify-content: center;
  background-position: center bottom;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
}
.container {
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
}
// .title {
//   font-size: 48px;
// }
.tooltip {
  display: flex;
  gap: 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  /* identical to box height, or 28px */

  text-align: center;

  color: #ffffff;

  img {
    width: 15px;
  }
}
.tip {
  div {
    padding: 10px;
    h2 {
      font-weight: 700;
      font-size: 18px;
      line-height: 16px;
      /* identical to box height, or 67% */

      text-transform: uppercase;

      color: #ffffff;
    }
    p {
      margin-top: 15px;
      color: #ffffff;
      font-size: 16px;
      line-height: 20px;
      opacity: 0.5;
    }
  }
}

@media (max-width: var.$mobile-break-point) {
  .container {
    padding-top: 80px;
    min-height: 80vh;
  }
}
