.StatsItem {
  width: 100%;
  border: 2px solid #545454;
  background-color: rgba($color: #000000, $alpha: 0.3);
  border-radius: 10px;
  display: flex;
  overflow: hidden;
}
.statsIconWrap {
  border-right: 2px solid #545454;
  background: #08021f;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 10px;
  img {
    width: 30px;
  }
}
.statsTextWrap {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  padding: 10px 0;
  h2 {
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    /* identical to box height, or 18px */
    text-align: center;
  }
  p {
    font-weight: 700;
    font-size: 22px;
    line-height: 100%;
    text-align: center;
    color: #e7e7e7;
  }
}
