:global(.ant-alert) {
  background: linear-gradient(180deg, #131315 0%, #130a16 100%);
  padding: unset !important;
}
:global(.anticon-close) {
  color: white !important;
  opacity: 0.5 !important;
  font-size: 15px;
  margin: 20px 20px 0 0;
}
.alert {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 25px;
  overflow: hidden;
}
.textWrap {
  display: flex;
  align-items: center;
  background: #050306;
  border-radius: 20px;
  padding: 15px;
  gap: 25px;
}
.titleWrap {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
}
.addressTitle,
.address {
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  white-space: nowrap;
}

.desc {
  font-size: 14px;
  line-height: 20px;
  color: #ffffff;
  opacity: 0.5;
  max-width: 500px;
  font-weight: 600;
}

@media (max-width: 900px) {
  .icon {
    display: none;
  }
  .textWrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    background-color: transparent;
    padding: 15px 0 15px 15px;
  }
  .address {
    // overflow: scroll;
    max-width: 300px;
    overflow: scroll;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */

    &::-webkit-scrollbar {
      display: none;
    }
  }
}
